.books-container{
    background-color:#967E76;
    padding: 1rem 0 5rem 0rem;
    text-align: center;
}
.books-container > h3{
    margin-top: 2.3rem;
    color: #291109;
}
.books-container > h2{
    color: #291109;
}
.book-intro-text-container{
    padding: .1rem 3rem;
    line-height: 1.9rem;
    margin-bottom: 2rem;
    color: #3f251ef1;
}
.book-line{
    border-color: #291109;
    width: 50%;
}
.book-intro-text{
    font-family: sans-serif;
}
.books-card{
    background-color:#725c55;
    padding: 6.2rem 4rem 6.2rem 4rem;
    margin: 1rem;
    border-radius: 50%;
    color: rgb(230, 219, 219);
    box-shadow: #725c55 .1rem .1rem 2rem .1rem;
    transition: margin .5s;
}
.books-card:hover{
    margin: 1.5rem;
}
.books-card > h3{
    font-size: 1rem;
}
.books-card > h4{
    margin-top: 2.4rem;
    font-size: .84rem;
}
@media screen and (max-width: 600px) {
    .books-container{
        padding: 7rem 1rem 4rem 1rem;
    }
    .books-card{
        padding: 5rem 2rem ;
    }
}