.free-tool-image{
    height: 5rem;
}
.tools-container{
    background-color: #F8EDE3;
    padding:4rem 0rem 5rem 0rem;
    text-align: center;
}
.tools-container > h2{
    margin-bottom: 2rem;
}
.card-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
.tool-card{
    background-color: #D0B8A8;
    padding: 3rem 7rem 3rem 7rem;
    margin: 1rem 2rem 1rem 2rem;
}
.free-tool-title{
    font-size: 1.2rem;
}
.free-tool-sub-title{
    font-size: .9rem;
}
@media screen and (max-width: 600px) {
    .tool-card{
        padding: 2rem;
    }
    .tools-container{
        padding: 7rem 0rem 4rem 0rem;
    }
    .tools-container > h2{
        margin-bottom: 3rem;
    }
}
