.title-container{
    background-image: url('./title-background.jpg');
    background-size: cover;
    padding: 25rem 1rem 26rem 1rem;
    text-align: center;
    line-height: 3rem;
    font-family: 'Raleway',sans-serif;
}
.title-header{
    font-size: 2rem;
}
.title-sub-header{
    font-size: 1.5rem;
}
.title-sub-sub-header{
    font-size: 1.2rem
}

@media screen and (max-width:600px) {
    .title-container{
        padding: 25rem 2rem 25rem 2rem;
    }
}
