.nav-container{
    background-color: #7D6E83;
    display: flex;
    padding: .1rem;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    box-shadow: #534258 0rem .1rem 1.2rem .1rem;
}
.nav-items{
    display: flex;
}
.nav-brand{
    padding: 1rem;
}
.nav-brand > a{
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-family: 'Raleway';
    transition: color .5s;
    font-weight: 600;
    font-size: 1.1rem;
}
.nav-brand > a:hover{
    color: #432f49;
}
.nav-items > li{
    list-style: none;
    margin-right: 1rem;
    padding: 0.1rem;
}
.nav-items > li > a {
    text-decoration: none;
    color: rgb(0, 0, 0);
    font-size: .9rem;
    transition: color .5s;
    font-weight: 500;
    font-size: .9rem;
}
.nav-items > li > a:hover {
    color: rgb(121, 82, 122);
}
.nav-items > li > a:active{
    color: rgb(159, 130, 187);
}





@media  screen and (max-width: 600px) {
    .nav-container{
        display:initial;
        text-align: center;
        justify-self: unset;
        padding: .3rem;
    }
    .nav-brand{
        padding: .2rem;
        margin-top: .8rem;
    }
    .nav-brand > a{
        font-size: 1.44rem;
    }
    .nav-items{
        padding: .1rem;
        justify-content: center;
        position: relative;
    }
}

